import { ref } from 'vue'
import { API_URL } from '@/config'

export const userStore = {
  currentUser: ref(null),

  // Initialize the store by checking localStorage
  initializeStore() {
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      try {
        const parsedUser = JSON.parse(storedUser);
        
        // Ensure all expected properties exist
        const completeUser = {
          firstName: parsedUser.firstName,
          middleName: parsedUser.middleName,
          lastName: parsedUser.lastName,
          password: parsedUser.password,
          intention: parsedUser.intention || '',
          day: parsedUser.day || 0,
          dateLastCompleted: parsedUser.dateLastCompleted || '',
          total: parsedUser.total || 0,
          petition: parsedUser.petition !== undefined ? parsedUser.petition : true,
          thanksgiving: parsedUser.thanksgiving !== undefined ? parsedUser.thanksgiving : false,
          flameOfLove: parsedUser.flameOfLove !== undefined ? parsedUser.flameOfLove : true,
          totalNovenas: parsedUser.totalNovenas || 0,
          securityQuestion: parsedUser.securityQuestion || ''
        };

        this.currentUser.value = completeUser;
      } catch (error) {
        console.error('Error parsing stored user:', error);
        localStorage.removeItem('user');
      }
    }
  },

  // Set user after successful login
  setUser(user) {
    // Ensure all properties are explicitly set
    const completeUser = {
      firstName: user.firstName,
      middleName: user.middleName,
      lastName: user.lastName,
      password: user.password,
      intention: user.intention || '',
      day: user.day || 0,
      dateLastCompleted: user.dateLastCompleted || '',
      total: user.total || 0,
      petition: user.petition !== undefined ? user.petition : true,
      thanksgiving: user.thanksgiving !== undefined ? user.thanksgiving : false,
      flameOfLove: user.flameOfLove !== undefined ? user.flameOfLove : true,
      totalNovenas: user.totalNovenas || 0, 
      securityQuestion: user.securityQuestion || ''
    };

    this.currentUser.value = completeUser;
    localStorage.setItem('user', JSON.stringify(completeUser));
  },

    // Update user's day after completing a novena
    async updateUserDay(newDay) {
      if (!this.currentUser.value) {
        throw new Error('No user logged in');
      }
    
      try {
        const today = new Date().toISOString().split('T')[0];
        const newTotal = this.currentUser.value.total + 1;
    
        const params = new URLSearchParams({
          path: 'update-day',
          firstName: this.currentUser.value.firstName,
          middleName: this.currentUser.value.middleName,
          lastName: this.currentUser.value.lastName,
          password: this.currentUser.value.password,
          day: newDay,
          dateLastCompleted: today,
          total: newTotal
        });
    
        const response = await fetch(`${API_URL}?${params.toString()}`);
        const data = await response.json();
    
        if (!data.success) {
          throw new Error(data.message || 'Failed to update day in database');
        }
    
        // Update local user object
        const updatedUser = {
          ...this.currentUser.value,
          day: newDay,
          dateLastCompleted: today,
          total: newTotal,
          totalNovenas: data.totalNovenas || this.currentUser.value.totalNovenas  // Update from response
        };
    
        this.currentUser.value = updatedUser;
        localStorage.setItem('user', JSON.stringify(updatedUser));
    
        return updatedUser;
      } catch (error) {
        console.error('Error updating user day:', error);
        throw error;
      }
    },

  // Clear user on logout
  async clearUser() {
    try {
      // Optional: Call logout endpoint if needed
      await fetch(`${API_URL}/logout`, {
        method: 'POST'
      });
    } catch (error) {
      console.error('Logout error:', error);
    }
    
    // Clear local storage and user store
    localStorage.removeItem('user');
    this.currentUser.value = null;
  },

  // Check if user exists (now just checks localStorage)
  isAuthenticated() {
    return !!this.currentUser.value;
  }
}

// Initialize store when imported
userStore.initializeStore();