<template>
    <transition name="slide-up">
      <div v-if="showPrompt" class="install-banner">
        <div class="banner-content">
          <p class="banner-text">Install the Rosary Novena app on your device for easy access!</p>
          <div class="banner-buttons">
            <button @click="install" class="install-button">Install</button>
            <button @click="dismiss" class="dismiss-button">Not Now</button>
          </div>
        </div>
      </div>
    </transition>
  </template>
  
  <script>
  export default {
    name: 'InstallPrompt',
    data() {
      return {
        showPrompt: false,
        deferredPrompt: null
      }
    },
    mounted() {
        // Check if already installed or dismissed
        const dismissed = localStorage.getItem('installPromptDismissed')
        const isStandalone = window.matchMedia('(display-mode: standalone)').matches
        
        // Check if mobile device
        const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
        
        if (!dismissed && !isStandalone && isMobile) {
            window.addEventListener('beforeinstallprompt', (e) => {
            e.preventDefault()
            this.deferredPrompt = e
            this.showPrompt = true
            })
        }
    },
    methods: {
      async install() {
        if (!this.deferredPrompt) return
        
        this.deferredPrompt.prompt()
        const { outcome } = await this.deferredPrompt.userChoice
        
        if (outcome === 'accepted') {
          console.log('App installed')
        }
        
        this.deferredPrompt = null
        this.showPrompt = false
      },
      dismiss() {
        localStorage.setItem('installPromptDismissed', 'true')
        this.showPrompt = false
      }
    }
  }
  </script>
  
  <style scoped>
