    text-align: center;
  }
  
  .banner-text {
    font-family: 'Libre Baskerville', serif;
    margin: 0 0 15px 0;
    font-size: 16px;
  }
  
  .banner-buttons {
    display: flex;
    gap: 10px;
    justify-content: center;
  }
  
  .install-button,
  .dismiss-button {
    font-family: 'Libre Baskerville', serif;
    padding: 10px 20px;
